import cx from 'classnames';
import { ChipGroupProps } from './ChipGroup.types';

const ChipGroup = ({
  className,
  orientation = 'horizontal',
  children,
}: ChipGroupProps) => {
  return (
    <div
      className={cx('flex flex-wrap gap-2', className, {
        'flex-col': orientation === 'vertical',
      })}
      data-testid="chip-group"
    >
      {children}
    </div>
  );
};

export { ChipGroup };
