import { Icon } from '../Icon';
import { BodyText } from '../Typography';
import { ChipProps } from './Chip.types';
import cx from 'classnames';

const Chip = ({
  id,
  label,
  clickable = false,
  deletable = false,
  onClick,
  onClose,
}: ChipProps) => {
  const chipId = id + '';

  const handleClick = () => {
    if (clickable && onClick) {
      onClick({ id: chipId });
    }
  };

  const handleClose = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    if (onClose) {
      onClose({ id: chipId });
    }
  };

  return (
    <div className="w-max flex items-center gap-1 bg-actions-secondary border border-borders z-10 px-1 py-0.5 rounded-md hover:bg-actions-secondary-hover">
      <button
        className={cx(
          'outline-none cursor-text focus-visible:border-2 focus-visible:border-actions-primary-active focus-visible:bg-actions-secondary-active focus-visible:rounded-md',
          {
            '!cursor-pointer': clickable,
          },
        )}
        onClick={handleClick}
        data-testid="button-chip"
      >
        <BodyText
          className={cx('pointer-events-none', {
            'cursor-pointer !text-actions-primary': clickable,
          })}
        >
          {label}
        </BodyText>
      </button>
      {deletable && (
        <button
          className="flex items-center appearance-none outline-none z-20 focus-visible:border-2 focus-visible:border-actions-primary-active hover:bg-actions-secondary-hover rounded-md p-0.5 cursor-pointer"
          onClick={handleClose}
          data-testid="button-delete"
        >
          <Icon name="off_outline_close" className="text-[20px]" />
        </button>
      )}
    </div>
  );
};

export { Chip };
