import { useEffect, useState, useRef } from 'react';
import cx from 'classnames';
import styles from './Slider.module.css';
import type {
  RangeSliderOutput,
  SingleSliderOutput,
  SliderError,
  SliderProps,
} from './Slider.types';
import { BodyText, ComponentLabel } from '../Typography';

const Slider = ({
  type = 'single',
  min = 0,
  max = 100,
  value,
  gap = 1,
  step = 1,
  withInputs = true,
  label,
  readonly = false,
  disabled = false,
  onChange,
}: SliderProps) => {
  const [minVal, setMinVal] = useState<number>(value?.[0] || min);
  const [maxVal, setMaxVal] = useState<number>(value?.[1] || max);
  const minValRef = useRef<HTMLInputElement>(null);
  const maxValRef = useRef<HTMLInputElement>(null);
  const range = useRef<HTMLDivElement>(null);
  const sliderRef = useRef<HTMLDivElement>(null);
  const [showMinThumbTooltip, setShowMinThumbTooltip] = useState(false);
  const [showMaxThumbTooltip, setShowMaxThumbTooltip] = useState(false);
  const isRange = type === 'range';

  const tempMin = isRange ? minVal : min;
  const tempMax = isRange ? maxVal : max;
  const isMinValValid = minVal >= min && minVal <= tempMax;
  const isMaxValValid = maxVal >= tempMin && maxVal <= max;

  const getPercent = (value: number) =>
    Math.round(((value - min) / (max - min)) * 100);

  // Calculate position for tooltip based on thumb value
  const calculatePosition = (value: number) => {
    const slider = sliderRef.current;
    if (!slider) return 0;
    // Offset width minus thumb width
    const width = slider.offsetWidth - 24;
    // Plus half thumb width to center
    const newPosition = ((value - min) / (max - min)) * width + 12;
    return newPosition;
  };

  const onMinThumbChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = Math.min(
      +event.target.value,
      !isRange ? maxVal : maxVal - gap,
    );
    setMinVal(Math.round(value / step) * step);
  };

  const onMaxThumbChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = Math.max(+event.target.value, minVal + gap);
    setMaxVal(Math.round(value / step) * step);
  };

  useEffect(() => {
    setShowMinThumbTooltip(!withInputs);
    setShowMaxThumbTooltip(!withInputs && isRange);
  }, [withInputs, isRange]);

  // Calculate range bar width based on thumb values
  useEffect(() => {
    if (!isRange) {
      const minPercent = getPercent(minVal);
      if (range.current) {
        range.current.style.left = '0';
        range.current.style.width = `${minPercent <= max ? minPercent : max}%`;
      }
    } else {
      const tempMinValue = isMinValValid ? minVal : min;
      const tempMaxValue = isMaxValValid ? maxVal : max;

      const minPercent = getPercent(tempMinValue);
      const maxPercent = getPercent(tempMaxValue);
      if (range.current) {
        range.current.style.left = `${minPercent}%`;
        range.current.style.width = `${maxPercent - minPercent}%`;
      }
    }

    if (!isMinValValid || !isMaxValValid) {
      onChange({
        error: true,
        message: `Invalid value, please check the range values (min: ${min}, max: ${max}, currentMin: ${minVal}, currentMax: ${maxVal})`,
      } as SliderError);

      return;
    }

    const values = isRange
      ? ({ min: minVal, max: maxVal } as RangeSliderOutput)
      : ({ value: minVal } as SingleSliderOutput);
    onChange(values);
  }, [minVal, maxVal, type, min, max]);

  return (
    <div
      className={cx('flex flex-col gap-3', {
        'gap-9': !withInputs,
      })}
    >
      {label && <ComponentLabel>{label}</ComponentLabel>}

      <div
        className={cx('flex justify-center items-center gap-2', {
          'flex-row-reverse': !isRange,
        })}
      >
        {/* TODO: Inputs will be replaced by Silica Input */}
        {withInputs && (
          <input
            type="number"
            min={min}
            max={isRange ? maxVal : max}
            value={Number(minVal).toString()}
            step={step}
            readOnly={readonly}
            disabled={disabled}
            onChange={(newValue) =>
              setMinVal(Number(newValue.target.value.toString()))
            }
            className={cx(
              'w-[62px] h-[34px] flex justify-center items-center text-center px-2 py-1 rounded bg-backgrounds-white border border-borders focus:border-actions-primary hover:shadow-elevation-10 outline-none',
              {
                'text-neutrals-tertiary border-none': readonly,
                'text-neutrals-disabled border-borders-disabled cursor-not-allowed':
                  disabled,
                'border-borders-danger focus:border-actions-danger-active':
                  !isMinValValid,
              },
            )}
          />
        )}
        <div className="relative w-full" ref={sliderRef}>
          {isMinValValid && (
            <div className="group">
              <input
                type="range"
                data-testid="min-slider"
                min={min}
                max={max}
                value={minVal}
                ref={minValRef}
                onChange={onMinThumbChanged}
                className={cx(
                  `${styles.Thumb} absolute h-0 w-full outline-none pointer-events-none z-[25]`,
                  {
                    'z-30': minVal > max - 100,
                  },
                )}
                disabled={disabled}
              />
              {showMinThumbTooltip && (
                <BodyText
                  className={cx(
                    'absolute z-20 -translate-x-1/2 -translate-y-9 hidden group-hover:block',
                    {
                      '!text-actions-primary-disabled': disabled,
                    },
                  )}
                  style={{
                    left: `${calculatePosition(minVal)}px`,
                  }}
                  color="text-actions-primary"
                >
                  {minVal}
                </BodyText>
              )}
            </div>
          )}
          {isRange && isMaxValValid && (
            <div className="group">
              <input
                type="range"
                data-testid="max-slider"
                min={min}
                max={max}
                value={maxVal}
                step={step}
                ref={maxValRef}
                onChange={onMaxThumbChanged}
                className={cx(
                  styles.Thumb,
                  'absolute h-0 w-full outline-none pointer-events-none z-20',
                  {},
                )}
                disabled={disabled}
              />
              {showMaxThumbTooltip && (
                <BodyText
                  className={cx(
                    'absolute z-20 -translate-x-1/2 -translate-y-9 hidden group-hover:block',
                    {
                      '!text-actions-primary-disabled': disabled,
                    },
                  )}
                  style={{
                    left: `${calculatePosition(maxVal)}px`,
                  }}
                  color="text-actions-primary"
                >
                  {maxVal}
                </BodyText>
              )}
            </div>
          )}
          <>
            <div className="relative w-full">
              {/* Track bar */}
              <div className="absolute w-full h-1 z-10 bg-backgrounds-overlay rounded-[3px]" />
              {/* Progress bar */}
              <div
                ref={range}
                className={cx('absolute h-1 z-10 bg-actions-primary', {
                  'bg-actions-primary-disabled': disabled || readonly,
                })}
              />
              {/* Minimum and maximum values (totals) */}
              <div className="absolute w-full h-full top-full pt-[16px]">
                <BodyText className="absolute">{min}</BodyText>
                <BodyText className="absolute right-0 text-right">
                  {max}
                </BodyText>
              </div>
            </div>
          </>
        </div>
        {/* TODO Inputs will be replaced by Silica Input component when ready */}
        {withInputs && isRange && (
          <input
            type="number"
            min={minVal}
            max={max}
            value={Number(maxVal).toString()}
            readOnly={readonly}
            disabled={disabled}
            onChange={(newValue) => setMaxVal(+newValue.target.value)}
            className={cx(
              'w-[62px] h-[34px] flex justify-center items-center text-center px-2 py-1 rounded bg-backgrounds-white border border-borders focus:border-actions-primary hover:shadow-elevation-10 outline-none',
              {
                'text-neutrals-tertiary border-none': readonly,
                'text-neutrals-disabled border-borders-disabled cursor-not-allowed':
                  disabled,
                'border-borders-danger focus:border-actions-danger-active':
                  !isMaxValValid,
              },
            )}
          />
        )}
      </div>
    </div>
  );
};

export { Slider };
