import classnames from 'classnames';

import { Icon } from '../Icon';
import { ComponentLabel } from '../Typography';
import type { BadgeProps, BadgeTypesWithBgColors } from './Badge.types';

const BG_COLOR_CLASSES_MAP = {
  positive: {
    gray: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-neutral-secondary',
    },
    red: { backgroundColor: 'bg-badges-gray', textColor: 'text-badges-red' },
    orange: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-badges-orange',
    },
    blue: { backgroundColor: 'bg-badges-gray', textColor: 'text-badges-blue' },
    green: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-badges-green',
    },
    darkgray: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-badges-darkgray',
    },
    purple: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-badges-purple',
    },
    neon: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-badges-green-neon',
    },
  },
  negative: {
    gray: {
      backgroundColor: 'bg-badges-gray',
      textColor: 'text-neutral-secondary',
    },
    darkgray: {
      backgroundColor: 'bg-badges-darkgray',
      textColor: 'text-white',
    },
    red: { backgroundColor: 'bg-badges-red', textColor: 'text-white' },
    orange: { backgroundColor: 'bg-badges-orange', textColor: 'text-white' },
    blue: { backgroundColor: 'bg-badges-blue', textColor: 'text-white' },
    green: { backgroundColor: 'bg-badges-green', textColor: 'text-white' },
    purple: { backgroundColor: 'bg-badges-purple', textColor: 'text-white' },
    neon: { backgroundColor: 'bg-badges-green-neon', textColor: 'text-white' },
  },
  simple: {
    gray: { textColor: 'text-neutral-secondary' },
    darkgray: { textColor: 'text-badges-darkgray' },
    red: { textColor: 'text-badges-red' },
    orange: { textColor: 'text-badges-orange' },
    blue: { textColor: 'text-badges-blue' },
    green: { textColor: 'text-badges-green' },
    purple: { textColor: 'text-badges-purple' },
    neon: { textColor: 'text-badges-green-neon' },
  },
};

export const Badge = ({
  testId = '',
  text,
  className,
  color = 'gray',
  iconName,
  type = 'negative',
}: BadgeProps): JSX.Element => (
  <div
    data-testid={testId}
    className={classnames(
      'py-0.5 min-w-[24px] justify-center rounded-3xl uppercase flex items-center gap-1',
      {
        [BG_COLOR_CLASSES_MAP[type as BadgeTypesWithBgColors][color]
          .backgroundColor]: type !== 'simple',
        'px-1': type === 'simple' || !text,
        'px-2': type !== 'simple' && text,
      },
      className,
    )}
  >
    {iconName && (
      <Icon
        data-testid="badge-icon"
        name={iconName}
        className={classnames(
          'text-base',
          BG_COLOR_CLASSES_MAP[type]?.[color].textColor,
        )}
      />
    )}
    {text && (
      <ComponentLabel
        color={BG_COLOR_CLASSES_MAP[type]?.[color].textColor}
        className="max-w-xs whitespace-nowrap overflow-hidden overflow-ellipsis"
      >
        {text}
      </ComponentLabel>
    )}
  </div>
);
