import classNames from 'classnames';
import { SVGProps, VFC } from 'react';
import './coolicons.css';
import {
  ArrowheadOutline,
  Asterisk,
  Attachment,
  Calculator,
  Camera,
  Circle,
  CrossHammerWrench,
  CrossLightning,
  Excavator,
  FilterApplied,
  Forward,
  History,
  LayersOutline,
  Lightning,
  Lock,
  MapLocationPin,
  PolygonalSelector,
  ProjectsList,
  Route,
  SortDown,
  SortUp,
  Spinner,
  Target,
  Ticket,
  Tilde,
  Uploading,
  Urbint,
  Video,
  WarningCircle,
  WorkerHelmet,
  Wrench,
  Ruler,
  MultiLocation,
} from './svgs';
import { IconProps } from './types';

/**
 * The icon set used in [Hard Hat](https://www.figma.com/file/3vBhbRPOUU0Mw6XAC7gGAf/Hard-Hat-DS?node-id=18%3A3666),
 * Urbint's design system.
 *
 * Most of the icons are implemented as fonts while others (`*`) are
 * implemented as `svg`s. In both cases the icons are presented inside of a
 * `<i>` tag so that they will inherit the surrounding text styles.
 *
 * All icons are currently provided by the
 * [coolicons icon set](https://coolicons.cool/).
 */
export const Icon = ({ name, className, ...props }: IconProps): JSX.Element => {
  let ChildIcon: VFC<SVGProps<SVGSVGElement>> | undefined;
  switch (name) {
    case 'arrowhead_outline':
      ChildIcon = ArrowheadOutline;
      break;
    case 'asterisk':
      ChildIcon = Asterisk;
      break;
    case 'attachment':
      ChildIcon = Attachment;
      break;
    case 'calculator':
      ChildIcon = Calculator;
      break;
    case 'camera':
      ChildIcon = Camera;
      break;
    case 'circle':
      ChildIcon = Circle;
      break;
    case 'cross_hammer_wrench':
      ChildIcon = CrossHammerWrench;
      break;
    case 'cross_lightning':
      ChildIcon = CrossLightning;
      break;
    case 'excavator':
      ChildIcon = Excavator;
      break;
    case 'filter_applied':
      ChildIcon = FilterApplied;
      break;
    case 'forward':
      ChildIcon = Forward;
      break;
    case 'history':
      ChildIcon = History;
      break;
    case 'layers_outline':
      ChildIcon = LayersOutline;
      break;
    case 'lightning':
      ChildIcon = Lightning;
      break;
    case 'lock':
      ChildIcon = Lock;
      break;
    case 'map_location_pin':
      ChildIcon = MapLocationPin;
      break;
    case 'polygonal_selector':
      ChildIcon = PolygonalSelector;
      break;
    case 'projects_list':
      ChildIcon = ProjectsList;
      break;
    case 'route':
      ChildIcon = Route;
      break;
    case 'sort_down':
      ChildIcon = SortDown;
      break;
    case 'sort_up':
      ChildIcon = SortUp;
      break;
    case 'spinner':
      ChildIcon = Spinner;
      break;
    case 'target':
      ChildIcon = Target;
      break;
    case 'ticket':
      ChildIcon = Ticket;
      break;
    case 'tilde':
      ChildIcon = Tilde;
      break;
    case 'uploading':
      ChildIcon = Uploading;
      break;
    case 'urbint':
      ChildIcon = Urbint;
      break;
    case 'video':
      ChildIcon = Video;
      break;
    case 'warning_circle':
      ChildIcon = WarningCircle;
      break;
    case 'worker_helmet':
      ChildIcon = WorkerHelmet;
      break;
    case 'wrench':
      ChildIcon = Wrench;
      break;
    case 'ruler':
      ChildIcon = Ruler;
      break;
    case 'multi_location':
      ChildIcon = MultiLocation;
      break;
  }
  return (
    <i
      className={classNames({ [`ci-${name}`]: !ChildIcon }, className)}
      aria-hidden
      {...props}
      children={ChildIcon ? <ChildIcon /> : undefined}
    />
  );
};
