import { useState, useLayoutEffect } from 'react';
import { createPortal } from 'react-dom';
import { PortalProps } from './Portal.types';

const createPortalAndAppendToBody = (portalId: string) => {
  const portalElement = document.createElement('div');
  if (portalId) portalElement.setAttribute('id', portalId);
  document.body.appendChild(portalElement);
  return portalElement;
};

const Portal = ({ id = 'silica-portal', children }: PortalProps) => {
  const [portalElement, setPortalElement] = useState<HTMLElement | null>(null);

  useLayoutEffect(() => {
    let element = document.getElementById(id);
    // if element is not found with id or id is not provided,
    // create and append to body
    if (!element) {
      element = createPortalAndAppendToBody(id);
    }
    setPortalElement(element);

    return () => {
      // delete the programatically created element
      if (element?.parentNode) {
        element.parentNode.removeChild(element);
      }
    };
  }, [id]);

  // portalElement state will be null on very first render.
  if (!portalElement) {
    return null;
  }

  // creates the actual portal
  return createPortal(children, portalElement);
};

export { Portal };
