import { Icon } from '../../Icon';
import { BadgeProps } from './Badge.types';

const Badge = ({
  label,
  title,
  className,
  iconStart,
  iconEnd,
}: BadgeProps): JSX.Element => {
  let labelMargin = '';
  if (!iconStart && !iconEnd) {
    labelMargin = 'mx-1';
  } else if (!iconStart) {
    labelMargin += 'ml-1 ';
  } else if (!iconEnd) {
    labelMargin += 'mr-1';
  }

  return (
    <div
      className={`flex items-center font-bold shadow-5 h-5 rounded-full p-0.5 w-max uppercase bg-brand-gray-20 ${className}`}
      role="note"
    >
      <span className="flex items-center px-1" title={title}>
        {iconStart && <Icon name={iconStart} />}
        {label && <span className={`text-tiny ${labelMargin}`}>{label}</span>}
        {iconEnd && <Icon name={iconEnd} />}
      </span>
    </div>
  );
};

export default Badge;
