import { BaseBannerProps } from './BaseBanner.types';

import cx from 'classnames';
import { Icon } from '../Icon';
import { ComponentLabel } from '../Typography';

import styles from './BaseBanner.module.css';

const BaseBanner = ({
  className,
  dataTestId = 'base-banner-component',
  type = 'info',
  children,
  large = false,
  icon,
  closeButton = false,
  onClose,
  linkLabel,
  onLinkClick,
}: BaseBannerProps) => {
  return (
    <div
      className={cx(
        `relative w-full flex items-center border ${styles[type]}`,
        className,
      )}
      data-testid={dataTestId}
    >
      <div
        className={cx('w-full flex items-center p-2 gap-2', {
          'p-4 gap-4': large,
        })}
      >
        {icon && (
          <div
            className={`w-6 h-6 rounded flex justify-center items-center ${
              styles['icon-' + type]
            }`}
          >
            <Icon
              name={icon}
              className={cx('text-neutrals-primary-light rounded text-[20px]', {
                'text-[40px]': large,
              })}
            />
          </div>
        )}
        <div
          className={cx(
            'flex-grow overflow-hidden truncate whitespace-nowrap',
            {
              '!whitespace-normal': large,
            },
          )}
        >
          {children}
        </div>
        <div className="flex justify-self-end">
          {linkLabel && (
            <button className="flex items-center" onClick={onLinkClick}>
              <ComponentLabel className="text-actions-primary cursor-pointer whitespace-nowrap">
                {linkLabel}
              </ComponentLabel>
            </button>
          )}
          {closeButton && (
            <button
              className="w-10 h-10 flex justify-center items-center focus-within:outline-none"
              onClick={onClose}
              data-testid="close-button"
            >
              <Icon name="close_big" />
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export { BaseBanner };
