import type { NetworkStatusBannerProps } from './NetworkStatusBanner.types';

import { useState } from 'react';

import { NetworkBannerStatus } from './NetworkStatusBanner.constants';
import { BaseBanner } from '../BaseBanner';

const NetworkStatusBanner = ({
  variant,
  icon,
  label,
  linkLabel,
  metadata,
  closeButton,
  onLinkClick,
  onClose,
}: NetworkStatusBannerProps) => {
  const [isOpen, setOpen] = useState(true);

  if (!isOpen) {
    return null;
  }

  const handleClose = () => {
    setOpen(false);
    onClose?.();
  };

  const handleLinkClick = () => {
    onLinkClick?.();
  };

  const {
    [variant]: {
      bannerType,
      icon: defaultIcon,
      label: defaultLabel,
      linkLabel: defaultLinkLabel,
      large,
      closeButton: defaultCloseButton,
    },
  } = NetworkBannerStatus;

  const replaceMetadataKeys = (label: string) => {
    if (!metadata) {
      return label;
    }

    let replacedLabel = label;
    Object.keys(metadata ?? {}).forEach((key) => {
      replacedLabel = replacedLabel.replace(
        `{${key}}`,
        (metadata[key] as string) || '',
      );
    });
    return replacedLabel;
  };

  const finalLabel = label
    ? replaceMetadataKeys(label)
    : replaceMetadataKeys(defaultLabel);

  return (
    <BaseBanner
      type={bannerType}
      icon={icon ? icon : defaultIcon}
      large={large}
      closeButton={closeButton ?? defaultCloseButton}
      onClose={handleClose}
      onLinkClick={handleLinkClick}
      linkLabel={linkLabel ? linkLabel : defaultLinkLabel}
    >
      {finalLabel}
    </BaseBanner>
  );
};

export { NetworkStatusBanner };
